"use client";
import { useState } from "react";

export default function ImageSlider({ images }: { images: string[] }){
  const [idx, setIdx] = useState(0);
  const prev = () => setIdx((p)=> (p===0 ? images.length-1 : p-1));
  const next = () => setIdx((p)=> (p===images.length-1 ? 0 : p+1));

  return (
    <div className="relative overflow-hidden rounded-md">
      <div
        className="flex transition-transform duration-300 ease-out"
        style={{ transform: `translateX(-${idx*100}%)`, width: `${images.length*100}%` }}
      >
        {images.map((src,i)=>(
          <img key={i} src={src} className="w-full aspect-square object-cover" alt={`slide-${i}`} />
        ))}
      </div>
      <button onClick={prev} className="absolute left-2 top-1/2 -translate-y-1/2 bg-card/80 rounded-md px-3 py-1 border border-divider">&lsaquo;</button>
      <button onClick={next} className="absolute right-2 top-1/2 -translate-y-1/2 bg-card/80 rounded-md px-3 py-1 border border-divider">&rsaquo;</button>
    </div>
  );
}
